.home-main{
    background-color: transparent;
    min-height: 62vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.7rem;
}

.description{
    width: 60vw;
    text-align: justify;
    line-height: 2rem;
    letter-spacing: 2px;
    background-color: white;
    padding: 5vh;
    font-weight: 700;
}

.lines{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100%;
    margin: auto;
    width: 90vw;
    z-index: -1;
}
  
.line{
    position: absolute;
    width: 1px;
    height: 100%;
    top: 0;
    left: 25%;
    background: rgba(255, 255, 255, 0.1);
    overflow: hidden;
}

.line::after{
    content: '';
    display: block;
    position: absolute;
    height: 15vh;
    width: 100%;
    top: -50%;
    left: 0;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #ffffff 75%, #ffffff 100%);
    animation: drop 7s 0s infinite;
    animation-fill-mode: forwards;
    animation-timing-function: cubic-bezier(0.4, 0.26, 0, 0.97);
}
  
.line:nth-child(1){
    margin-left: -25%;
}

.line:nth-child(1)::after{
    animation-delay: 2s
}
  
.line:nth-child(3){
    margin-left: 25%
}

.line:nth-child(3)::after{
    animation-delay: 1.7s
}

.line:nth-child(4){
    margin-left: 50%
}

.line:nth-child(4)::after{
    animation-delay: 3s
}

.line:nth-child(5){
    margin-left: 75%
}

.line:nth-child(5)::after{
    animation-delay: 1.2s
}
  
    
@keyframes drop{
    0%{
        top: -50%
    }
    100%{
        top: 110%
    }
}